<template>
  <div>
    <!-- 文章正文 -->
    <div
      style="
        background-color: whitesmoke;
        display: inline-block;
        vertical-align: top;
        width: 60%;
        margin-left: 12%;
        border-radius: 10px;
      "
    >
      <v-md-preview :text="text" ref="preview" />
    </div>

    <!-- 文章目录 -->
    <div
      style="
        position: fixed;
        overflow: auto;
      
        background-color: whitesmoke;
        display: inline-block;
        vertical-align: top;
        width: 20%;
        max-height:450px;
        margin-left: 2%;
        border-radius: 5px;
      "
    >
       <!-- <a-icon type="container" /> -->
      <div class="el-icon-tickets"  style="font-size: 20px;margin-left:12px;margin-top: 5px;color:dimgray"> 目录</div>
      <hr style="margin-top: 5px; margin-bottom: 5px" />
      <div
        id="catalog"
        v-for="anchor in titles"
        :style="{
          padding: `0px 0px 5px ${anchor.indent * 30}px`,
          marginLeft: '12px',
        }"
        @click="handleAnchorClick(anchor)"
        :key="anchor"
      >
        <a id="font" style="cursor: pointer; font-size: 16px"> {{ anchor.title }}</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "markdownComponent",
  props: ["id", "name"],
  data() {
    return {
      text: ``,
      titles: [],
    };
  },
  watch: {
    id: {
      handler() {
        //刷新页面,这样才能拿到axios请求后的数据。
        // location.reload();
        this.$router.go(0);
      },
    },
  },
  mounted() {
    //文章数据
    console.log(this.name);
    var url = encodeURI(
      "http://43.139.56.159:3001/uploadFiles/" + this.name + ".md"
    );
    // var urldeco = decodeURI(url);
    console.log(url);
    axios({
      method: "get", //请求方法
      url: url,
    })
      .then((res) => {
        this.text = res.data;
        console.log(this.text);
      })
      .then(() => {
        // console.log("这里执行了");
        const anchors =
          this.$refs.preview.$el.querySelectorAll("h1,h2,h3,h4,h5,h6");
        // console.log("呵呵", anchors);
        const titles = Array.from(anchors).filter(
          (title) => !!title.innerText.trim()
        );
        // console.log("哈哈", titles);

        if (!titles.length) {
          this.titles = [];
          return;
        }

        const hTags = Array.from(
          new Set(titles.map((title) => title.tagName))
        ).sort();

        this.titles = titles.map((el) => ({
          title: el.innerText, //文本内容
          lineIndex: el.getAttribute("data-v-md-line"), //？？？
          indent: hTags.indexOf(el.tagName), //标题级别-1
        }));
      })
      .catch((err) => {
        console.log("请求文章出错！");
        return Promise.reject(err);
      });
  },

  methods: {
    handleAnchorClick(anchor) {
      const { preview } = this.$refs;
      const { lineIndex } = anchor;

      const heading = preview.$el.querySelector(
        `[data-v-md-line="${lineIndex}"]`
      );

      if (heading) {
        preview.scrollToTarget({
          target: heading,
          scrollContainer: window,
          top: 50,
        });
      }
    },
  },
};
</script>

<style scoped>
#catalog {
}
#catalog:hover {
  background-color: gainsboro;
}
#catalog:hover #font{
  /* color:black; */
  color: #1890ff;
  font-weight:550;
}
</style>